import React from "react";
import Link from "next/link"; // Necesitas Link de Next.js para las rutas
import menuItems from "./menuItems";

const Menu = () => {
  return (
    <nav className="flex space-x-4">
      {/* 2. Mapear la data para crear los enlaces */}
      {menuItems.map((item) => (
        <Link
          key={item.id}
          href={item.href}
          className="text-gray-700 hover:text-blue-500 transition-colors"
        >
          {item.label}
        </Link>
      ))}
    </nav>
  );
};

export default Menu;
